.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 40px;
  padding-top: 2vw;
  padding-left: 5vw;
  padding-right: 4vw;
  padding-bottom: 2vw;
}
.img-wrap{
  overflow: hidden;
  width: 27.1vw;
  height: 42vh;
  position: relative;
  opacity: 0.8;
}
.img-wrap img{
  width: 27.1vw;
  height: 42vh;
  position: absolute;
  border-radius: 10px;
  top: 0;
  left: 0;
}

.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  z-index: 2;
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid white;
}

@media screen and (max-width: 900px){
  .img-grid{
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .img-wrap{
    width: 90vw;
    height: 35vh;
    position: relative;
    opacity: 1;
  }
  .img-wrap img{
    width: 90vw;
    height: 35vh;
    position: absolute;
    border-radius: 10px;
    top: 0;
    left: 0;
  }
  .backdrop{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .backdrop img{
    max-width: 90%;
    max-height: 95%;
    margin: 60px auto;
  }
}