.slider {
    z-index: -1;
margin-top: 30px;
margin-bottom: 30px;
position: relative;
height: min-content;
display: flex;
justify-content: center;
align-items: center;
object-fit: contain;
}

.image {
width: 70vw;
height: 70vh;
border-radius: 10px;
}

.right-arrow {
position: absolute;
top: 50%;
right: 32px;
font-size: 3rem;
color: #283557;
z-index: 10;
cursor: pointer;
user-select: none;
}

.left-arrow {
position: absolute;
top: 50%;
left: 32px;
font-size: 3rem;
color: #283557;
z-index: 10;
cursor: pointer;
user-select: none;
}

.slide {
opacity: 0;
transition-duration: 1s ease;
}

.slide.active {
opacity: 1;
transition-duration: 1s;
transform: scale(1.08);
}

