.about{
    text-align: left;
}
.paper_grids{
    display: grid;
    grid-template-columns: repeat(3,auto);
    gap: 30px;
    margin: 30px;
}
.paper_grid{
    width: 30vw;
    height: 48vh;
    padding: 20px;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-image: url("/src/components/images/background\ images/bgi (4).jpg");
    background-size: 500px;
    border: 1px solid lightgrey;
}

.paper_grid:hover{
    border: 1px solid #283557;
}

.paper_grid > h4{
    color: #283557;
    position: relative;
    top: 43%;
    left: 51%;
    transform: translate(-50%, -50%);
}
.paper_grid > p{
    color: #283557;
    position: relative;
    top: 32%;
    left: 50%;
    transform: translate(-50%, -50%);
}

@media screen and (max-width: 900px){
    .paper_grids{
        display: flex;
        flex-direction: column;
    }
    .paper_grid{
        width: 85vw;
        height: 45vh;
        justify-content: center;
        align-items: center;
        text-align: center;
        background-size: 800px;
    }
    .paper_grid > h4{
        top: 48%;
    }
    .paper_grid > p{
        position: relative;
        top: 35%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

}