@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.error{
    background: linear-gradient(to bottom, #283557, #e7e3e2);
    justify-content: center;
    align-items: center;
    padding: 30vh;
    height: fit-content;
    width: 70vw;
    margin: 30px auto;
}
.page_not_found{
    color: #283557;
    position: inherit;
    font-family: 'Great Vibes', cursive;
    text-shadow: 2px 2px rgb(255, 255, 255) ;
}
.page_not_found > button{
    color: #283557;
    font-family: 'Lobster', cursive;
}
.text_404{
    color: #283557;
    text-shadow: 2px 2px rgb(255, 255, 255) ;
}
.home_button{
    color: #283557;
    border: 4px solid #283557;
    border-radius: 4px;
    background: #ffffff;
    height: 10vh;
    padding-left: 4vw;
    padding-right: 4vw;
}

@media screen and (max-width: 900px){
    .error{
        background: linear-gradient(to bottom, #283557, #e7e3e2);
        justify-content: center;
        align-items: center;
        padding: 10vh;
        height: fit-content;
        width: 70vw;
        margin: 6vh auto;
    }
}