.grid{
    background: linear-gradient(to bottom, #283557, #e7e3e2);
    padding-top: 20px;
    padding-bottom: 20px;
}
.contact_paper{
    padding: 30px;
    height: fit-content;
    width: 70vh;
    margin: 30px auto;
}

@media screen and (max-width: 900px){
    .contact_paper{
        margin: 10% auto;
        width: 87vw;
    }
}