.slider {
    z-index: 0;
margin-top: 30px;
margin-bottom: 30px;
position: relative;
height: min-content;
display: flex;
justify-content: center;
align-items: center;
object-fit: contain;
}

.image {
width: 70vw;
height: 80vh;
border-radius: 10px;
}

.right-arrow {
position: absolute;
top: 45%;
right: 8vw;
font-size: 3rem;
color: #283557;
z-index: 1;
cursor: pointer;
user-select: none;
}

.left-arrow {
position: absolute;
top: 45%;
left: 8vw;
font-size: 3rem;
color: #283557;
z-index: 1;
cursor: pointer;
user-select: none;
}

.slide {
opacity: 0;
transition-duration: 1s ease;
}

.slide.active {
opacity: 1;
transition-duration: 1s;
transform: scale(1.08);
}

@media screen and (max-width: 900px){
    .slider {
    margin-top: 50px;
    margin-bottom: 50px;
    }
    .image {
    width: 70vw;
    height: 30vh;
    border-radius: 10px;
    }
    .right-arrow {
        top: 41%;
        right: 1vh;
    }
        
    .left-arrow {
    top: 41%;
    left: 1vh;
    }
    .slide.active {
    opacity: 1;
    transition-duration: 1.3s;
    transform: scale(1);
    }
    .slide {
        transform: scale(0.9);
    }
}