@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');

.image_slide{
    width: 60vw;
    height: 40vh;
}
.slide-container{
    position: relative;
    text-align: center;
    color: white;
}
.home_desc{
    background-color: rgba(0, 0, 0, 0.404);
    opacity: 0.9;
    position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 60vw;
  height: 39vh;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.home_desc > p{
    position: absolute;
    text-align: center;
}
.home_desc > h2{
  padding-top: 30px;
}
.company_title{
  color: #283557;
  font-family: 'Inconsolata', monospace;
}
div > h4{
  color: #283557;
}
.three_section{
  display: flex;
}
.first{
  flex: auto;
  background: linear-gradient(to left, #011522, white);
  height: 40vh;
}
.second{
  flex: auto;
  background: linear-gradient(to right, #011522, white);
  height: 40vh;
}

@media screen and (max-width: 900px){
  .image_slide{
    width: 90vw;
    height: 27vh;
}
  .home_desc{
    width: 90vw;
    height: 27vh;
}
.first{
  height: 27vh;
}
.second{
  height: 27vh;
}
.home_desc > h2{
  padding-top: 12px;
}
}