.footer{
    z-index: 0;
    color: #283557;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    padding-top: 20px;
    padding-bottom: 20px;
    background: #e7e3e2;
    height: max-content;
}

.footer_logo{
    padding-left: 30px;
    width: fit-content;
    height: fit-content;
}

.footer_logo > img{
    z-index: 0;
    top:0;
    left: 0;
    position: relative;
    width: 300px;
    height: 180px;
}

.pages{
    text-align: left;
    position: relative;
    display: flex;
    flex-direction: column;
}
.services{
    text-align: left;
    position: relative;
    display: flex;
    flex-direction: column;
}

.pages > span{
    position: relative;
    padding-top: 5px;
    padding-bottom: 5px;
}
h3{
    margin:0;
}

.services > span{
    position: relative;
    padding-top: 5px;
    padding-bottom: 5px;
}

.company_details{
    display: flex;
    flex-direction: column;
}

.contact_info{
    padding-right: 20px;
}

.page_link{
    color: inherit;
    text-decoration: none;
    position: inherit;
}

.page_link:hover{
    color: #4da8d4;
}

@media screen and (max-width: 900px){
    .footer{
        display: flex;
        flex-direction: column;
        justify-content: left;
        align-items: left;
    }
    .footer_logo > img{
        width: 90vw;
        height: 30vh;
    }
    .contact_info{
        text-align: left;
        padding-top: 30px;
        padding-left: 30px;
        padding-bottom: 30px;
    }
    .pages{
        padding-top: 30px;
        padding-left: 30px;
        padding-bottom: 30px;
    }
    .services{
        padding-top: 30px;
        padding-left: 30px;
        padding-bottom: 30px;
    }

}